import {
  ClipboardCopyButton,
  CodeBlock,
  CodeBlockAction,
  CodeBlockCode,
  ExpandableSection,
  ExpandableSectionToggle,
} from "@patternfly/react-core";
import { useEffect, useState } from "react";
import type { VoidFunctionComponent } from "react";

export type ClientType = "java" | "python" | "quarkus" | "springboot";

export type SampleCodeSnippetProps = {
  expandableCode?: string;
  codeBlockCode: string;
  codeSnippet?: ClientType;
};

export const SampleCodeSnippet: VoidFunctionComponent<
  SampleCodeSnippetProps
> = ({ expandableCode, codeBlockCode, codeSnippet }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const [copied, setCopied] = useState<boolean>(false);

  useEffect(() => {
    setIsExpanded(false);
  }, [codeSnippet]);

  const clipboardCopyFunc = (_event: any, text: string) => {
    navigator.clipboard
      .writeText(text.toString())
      .then(() => {
        setCopied(true);
      })
      .catch(() => {
        setCopied(false);
      });
  };

  const onToggle = (isExpanded: boolean) => {
    setIsExpanded(isExpanded);
  };

  const copyCode = expandableCode
    ? codeBlockCode + "\n" + expandableCode
    : codeBlockCode;

  const actions = (
    <CodeBlockAction>
      <ClipboardCopyButton
        id="basic-copy-button"
        textId="code-content"
        aria-label="Copy to clipboard"
        exitDelay={600}
        maxWidth="110px"
        variant="plain"
        onClick={(e) => clipboardCopyFunc(e, copyCode)}
      >
        {copied ? "Successfully copied to clipboard!" : "Copy to clipboard"}
      </ClipboardCopyButton>
    </CodeBlockAction>
  );

  return (
    <CodeBlock actions={actions}>
      <CodeBlockCode id="code-content">
        {codeBlockCode}

        <ExpandableSection
          isExpanded={isExpanded}
          isDetached
          contentId="code-block-expand"
        >
          {expandableCode}
        </ExpandableSection>
      </CodeBlockCode>
      {expandableCode ? (
        <ExpandableSectionToggle
          isExpanded={isExpanded}
          onToggle={onToggle}
          contentId="code-block-expand"
          direction="up"
        >
          {isExpanded ? "Show Less" : "Show More"}
        </ExpandableSectionToggle>
      ) : null}
    </CodeBlock>
  );
};
