import {
  Alert,
  Button,
  Flex,
  FlexItem,
  HelperText,
  HelperTextItem,
  Spinner,
  Split,
  SplitItem,
} from "@patternfly/react-core";
import {
  CheckCircleIcon,
  ExclamationTriangleIcon,
} from "@patternfly/react-icons";
import { useTranslation } from "@rhoas/app-services-ui-components";
import { forwardRef } from "react";
import type { Status } from "ui-models/src/models/kafka";
import {
  CreatingStatuses,
  DegradedStatuses,
  DeletingStatuses,
  ReadyStatuses,
  ResumingStatuses,
  SuspendedStatuses,
  SuspendingStatuses,
} from "ui-models/src/models/kafka";
import "./StatusLabel.css";

type StatusLabelProps = {
  value: Status;
  showWarning?: boolean;
  showError?: boolean;
  withPopover?: boolean;
};

/**
 *<strong> Introduction </strong>
 *These are the various statuses that can show in the Kafka instances table under the "Status" header.
 */
export const StatusLabel = forwardRef<HTMLButtonElement, StatusLabelProps>(
  (
    { value, showWarning = false, showError = false, withPopover = false },
    ref
  ) => {
    const { t } = useTranslation("kafka");

    const buttonVariant = withPopover ? "link" : "plain";

    switch (true) {
      case ReadyStatuses.includes(value):
        return (
          <div>
            <Split hasGutter className="mas-c-status">
              <SplitItem>
                <CheckCircleIcon className="mas-m-ready" />
              </SplitItem>
              <SplitItem>{t("statuses.ready")}</SplitItem>
            </Split>
          </div>
        );

      case CreatingStatuses.includes(value):
        switch (true) {
          case showWarning:
            return (
              <div>
                <Split hasGutter className="mas-c-status">
                  <SplitItem>
                    <Spinner size="md" />
                  </SplitItem>
                  <SplitItem>
                    <Button ref={ref} variant={buttonVariant} isInline>
                      {t("statuses.creating")}
                    </Button>
                  </SplitItem>
                </Split>
                <Alert
                  variant="warning"
                  isInline
                  isPlain
                  title="This is taking longer than expected."
                />
              </div>
            );
          case showError:
            return (
              <div>
                <Split hasGutter className="mas-c-status">
                  <SplitItem>
                    <Spinner size="md" />
                  </SplitItem>
                  <SplitItem>
                    <Button ref={ref} variant={buttonVariant} isInline>
                      {t("statuses.creating")}
                    </Button>
                  </SplitItem>
                </Split>
                <Alert
                  variant="danger"
                  isInline
                  isPlain
                  title={t("status_warning_or_error_title")}
                />
              </div>
            );
          default:
            return (
              <div>
                <Split hasGutter className="mas-c-status">
                  <SplitItem>
                    <Spinner size="md" />
                  </SplitItem>
                  <SplitItem>
                    <Button ref={ref} variant={buttonVariant} isInline>
                      {t("statuses.creating")}
                    </Button>
                    <Flex>
                      <FlexItem>
                        <HelperText>
                          <HelperTextItem variant="indeterminate">
                            {t("status_created_shortly_help")}
                          </HelperTextItem>
                        </HelperText>
                      </FlexItem>
                    </Flex>
                  </SplitItem>
                </Split>
              </div>
            );
        }

      case DegradedStatuses.includes(value):
        return (
          <div>
            <Split hasGutter className="mas-c-status">
              <SplitItem>
                <ExclamationTriangleIcon className="mas-m-degraded" />
              </SplitItem>
              <SplitItem>{t("statuses.degraded")}</SplitItem>
            </Split>
          </div>
        );

      case SuspendingStatuses.includes(value):
        return (
          <div>
            <p className="mas-m-deleting"> {t("statuses.suspending")}</p>
          </div>
        );
      case SuspendedStatuses.includes(value):
        return (
          <div>
            <Split hasGutter className="mas-c-status">
              <SplitItem>
                <ExclamationTriangleIcon className="mas-m-degraded" />
              </SplitItem>
              <SplitItem>
                <Button ref={ref} variant={buttonVariant} isInline>
                  {t("statuses.suspended")}
                </Button>
              </SplitItem>
            </Split>
          </div>
        );
      case ResumingStatuses.includes(value):
        return (
          <div>
            <Split hasGutter className="mas-c-status">
              <SplitItem>
                <Spinner size="md" />
              </SplitItem>
              <SplitItem>{t("statuses.resuming")}</SplitItem>
            </Split>
          </div>
        );
      case DeletingStatuses.includes(value):
        return (
          <div>
            <p className="mas-m-deleting"> {t("statuses.deleting")}</p>
          </div>
        );
      default:
        return null;
    }
  }
);
